<template>
  <div class="ty">
    <div class="footerNav">
      <ul>
        <router-link to="/" tag="li">
          <a href=""><i class="nav-home"></i><span>首页{{$route.path}}</span></a>
        </router-link>
        <router-link to="/article" tag="li">
          <a href=""><i class="nav-find"></i><span>发现</span></a>
        </router-link>
        <router-link to="/download" tag="li">
          <a href=""><i class="nav-down"></i><span>下载</span></a>
        </router-link>
        <router-link to="/myNoLogin" tag="li">
          <a href=""><i class="nav-myInfo"></i><span>我的</span></a>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'footerNav'
}
</script>

<style>

/* 底部导航 */
.footerNav{width:100%;height:1.361111rem;background-color:#fff;box-shadow:0 -0.194444rem 0.416667rem #eee;position:fixed;left:0;bottom:0;z-index:999}
.footerNav ul{display:-webkit-flex;display:flex}
.footerNav ul li{flex:1;height:1.361111rem;text-align:center}
.footerNav ul li a{display:block;height:1.361111rem;font-size:0;color:#2b333b;overflow:hidden}
.footerNav ul li a span{display:block;font-size:0.25rem}
.footerNav ul li a i{display:block;width:0.638889rem;height:0.638889rem;margin:0.208333rem auto 0.055556rem;background:url(../../static/images/fNavIcon.png) no-repeat;background-size:auto 1.277778rem}
.footerNav ul li a i.nav-home{background-position:0 0}
.footerNav ul li a i.nav-find{background-position:-0.638889rem 0}
.footerNav ul li a i.nav-down{background-position:-1.277778rem 0}
.footerNav ul li a i.nav-myInfo{background-position:-1.916667rem 0}
.footerNav ul li.router-link-exact-active a{color:#f01414}
.footerNav ul li.router-link-exact-active .nav-home{background-position:0 -0.638889rem}
.footerNav ul li.router-link-exact-active .nav-find{background-position:-0.638889rem -0.638889rem}
.footerNav ul li.router-link-exact-active .nav-down{background-position:-1.277778rem -0.638889rem}
.footerNav ul li.router-link-exact-active .nav-myInfo{background-position:-1.916667rem -0.638889rem}

</style>



