        box-shadow: 0 0.055556rem 0.222222rem 0 #9dcc29;
        background: #9dcc29 url(../../static/images/cardBg3.jpg) no-repeat right;
      }
      .card-bg4 {
        box-shadow: 0 0.055556rem 0.222222rem 0 #0070bb;
        background: #0070bb url(../../static/images/cardBg4.jpg) no-repeat right;
      }
      .card-bg5 {
        background: #fff url(../../static/images/cardBg5.png) no-repeat right;
      }
      .card-bg6 {
        background: #fff url(../../static/images/cardBg6.png) no-repeat right;
      }
      .card-bg7 {
        box-shadow: 0 0.055556rem 0.222222rem 0 #ff5fb7;
        background: #ff5fb7 url(../../static/images/cardBg7.jpg) no-repeat right;
      }
    }
    .box-item {
      .s-card {
        margin-bottom: 0.666667rem;
        a {
          display: block;
          .img {
            width: 3.0rem;
            height: 2.0rem;
            border-radius: 0.083333rem;
          }
          .info {
            width: 5.0rem;
            margin-left: 0.333333rem;
            .tit {
              font-size: 0.388889rem;
              line-height: 0.527778rem;
              color: #2b333b;
              word-break: break-all;
              word-wrap: break-word;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
            }
            span {
              display: block;
              margin-top: 0.277778rem;
            }
            .desc {
              font-size: 0.305556rem;
              color: #71777D;
            }
            .price {
              font-size: 0.277778rem;
              color: #333b42;
            }
          }
        }
      }
    }
  }
  /deep/ .swiper-slide {
    width: 95%;
    margin-right: 0.083333rem;
    margin-bottom: 0.694444rem;
  }
}

/* 新上好课 */
.model {
  h3.yellow {
    .arrow::after {
      width: 0.222222rem;
      height: 0.222222rem;
      background: url(../../static/images/ranking.png) no-repeat;
      background-size: 100%;
    }
    .more {
      color: #ffa01e;
    }
  }
  /deep/ .swiper-pagination-bullet {
    width: 0.222222rem;
    height: 0.222222rem;
    background-color: #D9DDE1;
    transition: all .3s;
    opacity: 1;
  }
  /deep/ .swiper-pagination-bullet-active {
    width: .85333333rem;
    background: #B7BBBF;
    border-radius: 0.222222rem;
  }
}

/* 广告1 */
.commend-ad {
  padding: 0.666667rem 0.555556rem;
  img {
    width: 100%;
    height: 100%;
  }
}

</style>



