import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Article from '@/components/Article'
import Download from '@/components/Download'
import MyNoLogin from '@/components/MyNoLogin'
import Login from '@/components/Login'
import Register from '@/components/Register'
import ArtCont from '@/components/ArtCont'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/article',
      name: 'Article',
      component: Article
    },
    {
      path: '/download',
      name: 'Download',
      component: Download
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/myNoLogin',
      name: 'MyNoLogin',
      component: MyNoLogin
    },
    {
      path: '/artCont',
      name: 'artCont',
      component: ArtCont
    },
  ]
})



